.preloader {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 15;
	height: 100%;
    width: 0;
	background: $color-low-gray;
	transition: all 0.5s ease-out;
    opacity: 1;
    visibility: hidden;

    &__image {
        position: fixed;


        @include mobile {
            max-width: vw(200, $mobile);
            bottom: vw(-200, $mobile);
            left: vw(-200, $mobile);
        }

        @include tablet{
            max-width: vw(250, $tablet);
            bottom: vw(-250, $tablet);
            left: vw(-250, $tablet);
        }
        @include desktop {
            max-width: vw(300, $desktop);
            bottom: vw(-300, $desktop);
            left: vw(-300, $desktop);
        }
        @include laptop{
            max-width: vw(590);
            bottom: vw(-590);
            left: vw(-590);
        }
    }
}

.preloader--active {
	position: fixed;
	z-index: 15;
    opacity: 1;
    visibility: visible;
    width: 100%;
}

